body { font-family: 'Inter', sans-serif; background-color: #fdfbf6; color: #1a202c; }
.font-kalam { font-family: 'Kalam', cursive; }
#app-shell { max-width: 56rem; margin-left: auto; margin-right: auto; padding: 2.5rem 1rem; }
.app-container { background-color: #ffffff; border-radius: 0.75rem; box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04); padding: 1.5rem; }
@media (min-width: 640px) { .app-container { padding: 2rem; } }
.btn-primary, .btn-secondary { border-radius: 0.5rem; font-weight: 600; padding: 0.75rem 1.5rem; text-transform: uppercase; font-size: 0.875rem; transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1); box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06); display: inline-flex; align-items: center; justify-content: center; }
.btn-primary:hover, .btn-secondary:hover { transform: translateY(-2px); box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05); }
.btn-primary:active, .btn-secondary:active { transform: translateY(1px); box-shadow: none; }
.btn-primary:disabled, .btn-secondary:disabled { opacity: 0.6; cursor: not-allowed; transform: none; box-shadow: none; }
.btn-primary { background-color: #2563eb; color: #ffffff; }
.btn-primary:hover { background-color: #1d4ed8; }
.btn-primary:disabled { background-color: #a0aec0; }
.btn-secondary { background-color: #e2e8f0; color: #2d3748; }
.btn-secondary:hover { background-color: #cbd5e0; }
.input-primary { background-color: #ffffff; border: 1px solid #cbd5e0; color: #1a202c; padding: 0.75rem; border-radius: 0.5rem; width: 100%; font-family: 'Inter', sans-serif; transition: all 0.3s; }
.input-primary:focus { outline: none; border-color: #2563eb; box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.3); }
.input-styled { border: 2px solid #93c5fd; text-align: center; padding: 2px 8px; outline: none; transition: all 0.2s; width: 150px; font-weight: 600; border-radius: 0.375rem; background-color: #eff6ff; }
.input-styled:focus { border-color: #2563eb; background-color: #ffffff; box-shadow: 0 0 0 2px rgba(37, 99, 235, 0.2); }
.input-styled.incorrect { border-color: #ef4444; background-color: #fee2e2; }
.loader { width: 50px; height: 50px; border: 4px solid #2563eb; border-bottom-color: transparent; border-radius: 50%; display: inline-block; box-sizing: border-box; animation: rotation 1s linear infinite; }
@keyframes rotation { 0% { transform: rotate(0deg); } 100% { transform: rotate(360deg); } }
.phase-title { font-family: 'Kalam', cursive; color: #1d4ed8; font-weight: 700; font-size: 1.75rem; line-height: 2.25rem; }
.ai-feedback { background-color: #fef9c3; border-left-width: 4px; border-color: #facc15; border-radius: 0.25rem; padding: 1rem; margin-top: 1rem; color: #713f12; line-height: 1.6; }
